.mainHeader {
    max-width: 1920px;
    background-color: #161616;

}

.headerContent {
    padding-top: 20px;
    padding-bottom: 20px;

    display: grid;

    grid-template-columns: repeat(12, 1fr);
    grid-template-areas: "none none header  header  header   header  header  header  header glassLang burger";
}


.headerLogo {

    grid-area: header;

}

.headerGlassAndLang {
    margin: 0;
    grid-area: glassLang;
    padding-top: 35px;

}
.headerBurgerMenu {
    grid-area: burger;
    padding-top: 42px;
    margin-left: 5px;

}

.headerGlassImg {
    margin-top: 10px;
    padding-left: 38px;
}



.headerRusEng {
    color: white;
    margin-top: 5px;


}