

.body {
    max-width: 1226px;
    margin: 0 auto;
    padding-bottom-bottom: 20px;

}

.questionsContent {
    margin-top: 230px;
}

.questionsContent h3{
    margin-left: 10px;
    text-transform: uppercase;
}
.questionsContent p{
    margin-left: 10px;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
}


details > summary {
    padding: 5px;
    color: #333;
    border-top: 2px black solid;
    cursor: pointer;

    list-style-type: plus;  /* задаем маркер*/
}

/* Убираем стандартный маркер Chrome */
details summary::-webkit-details-marker {
    display: none
}
/* Убираем стандартный маркер Firefox */
details > summary {
    list-style: none;
}

/* Добавляем собственный маркер для закрытого состояния */
details summary:before {
    content: '\f146';
    margin-left: 80%;

}

/* Добавляем собственный маркер для открытого состояния */
details[open] summary:before {
    content: '\f146';
}



@media (max-width: 900px) {
    .questionsContent h2{
        text-align: center;
    }
}
