.body {
    max-width: 1290px;
    margin: 0 auto;

}

.askQuestionFormContainer {
    margin-top: 230px;
    margin-left: 20px;


}
.askQuestionFormContent {
    border: 1px solid #E4E7E9;
    padding-bottom: 25px;
}

.askQuestionFormContainer h2{
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: 1px;
    color: #18181A;
    text-align: left;
    margin-left: 20px;

}

.askQuestionFormModal {
    max-width: 912px;
    height: 602px;
    border-top: 40px solid #FFD43C;
}

.mainForm {
    background-color: #ffffff;
    border-radius: 5%;
    margin-left: 30px;

}
.mainFormInput {
    width: 95%;
    height: 43px;
    border: 2px solid #E4E7E9;
    margin-top: 25px;
    font-size: 16px;


}
.mainFormLabel {
    color: black;
    text-transform: none;
    background-color: white;
    margin-top: 15px;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;


}

.mainFormCheckBoxAndBtn {
    margin-top: 24px;
}


.mainFormButton {
    width: 100%;
    color: #18181A;
    background-color: #FFD43C;
    margin-left: 165px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 32px;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    border: none;
    padding: 10px 10px 10px 10px;
}


@media (max-width: 760px) {
    .mainFormButton {
        margin-left: 0;

    }
    .askQuestionFormContainer {
        margin-left: 0px;
    }
    
}


.mainFormButton:hover {
    background-color: burlywood;
    cursor: pointer;
}
.mainFormButton:focus {
    outline: none;
}

.mainFormItTextArea {
    border: 2px solid #E4E7E9;
    margin-top: 20px;
    width: 94%;
    height: 140px;
    padding-top: 10px;
    padding-left: 10px;
    font-family: Zona Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;

}

.mainFormCheckBox {
    margin-right: 10px;


}


input[type="checkbox"] {
    display: none;
}

input[type="checkbox"] {
    display: inline-block;
    width: 19px;
    height: 19px;
    vertical-align: middle;
    border: 2px solid black;
    cursor: pointer;
}




