.body {
    max-width: 1290px;
    margin: 0 auto;
}

.bodyContent {
    margin-top: 230px;
    margin-left: 20px;
}

h1 {
    text-align: left;
    margin-left: 30px;
    font-weight: bold;
}

.mainBlock {
    margin-top: 48px;
    margin-left: 30px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "One Two Three";
    margin-top: 0px;

}
@media(max-width: 1290px) {
    .body {
        max-width: 970px;
    }
    .mainBlock {
        margin-left: 12%;
        grid-template-areas: "One Two"
                              "Three none";
    }
    h1 {
        text-align: center;
        font-weight: bold;
    }
    .blockOne {
        margin-bottom: 10px;
    }
    .blockTwo {
        margin-bottom: 10px;

    }
    .blockThree {
        margin-bottom: 10px;
    }
}
@media(max-width: 970px) {
    .body {
        max-width: 750px;
    }
    .mainBlock {
        margin-left: 25%;
        grid-template-areas: "One" "Two"
                              "Three";
    }
    h1 {
        text-align: center;
        font-weight: bold;
    }
    .bodyContent {
        margin-top: 130px;
        margin-left: 40px;
    }
}
@media(max-width: 750px) {
    .body {
        max-width: none;
    }
    .mainBlock {
        margin: 0 auto;
        grid-template-areas: "One" "Two"
                              "Three";
    }
    h1 {
        text-align: center;
        font-weight: bold;
        font-size: 30px;
    }
    .blockOne {
        max-width: 200px;
        margin-bottom: 10px;
    }
    .blockTwo {
        max-width: 200px;
        margin-bottom: 10px;

    }
    .blockThree {
        max-width: 200px;
        margin-bottom: 10px;
    }
    .bodyContent {
        margin-top: 130px;
        margin-left: 8px;
    }
}


.blockOne {
    grid-area: One;
