.body {
    max-width: 1290px;
    margin: 0 auto;

}
.dayOpenDoorsContent {
    margin-top: 230px;
}

.dayOpenDoorsContent h2{
    margin-left: 50px;
    margin-bottom: 50px;
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: 1px;
}

.preview {
    max-width: 1126px;
    max-height: 520px;
    margin-left: 50px;
    position:relative

}

@media (max-width: 1919px) {
    .picture {
        display: none;
    }
}

@media (max-width: 1290px) {
    .preview {
        max-width: 1126px;
        max-height: 520px;
    }

}
@media (max-width: 900px) {
    h2 {
        text-align: center;
    }
    .preview {
        margin-left: 5px;
        max-width: 380px;
        max-height: 150px;
    }
}

.picture {
    position:absolute;
     left: -48%;
    bottom: -115.5%;


    width: 100%;
    height: 100%;
    z-index: -1;
    /*max-width:;*/
}