.body {
    max-width: 1920px;
    margin: 0 auto;
    background-color: #161616;
    height: 900px;
    background-color: #161616;
}

.sliderContainer {
    margin-top: 230px;
    margin-left: 347px;
    max-width: 1290px;
    height: 800px;
}
.sliderContent {
    padding-top: 72px;
}
@media(max-width: 1600px) {

    .sliderContainer {
        margin-left: 200px;
        padding: 0;
    }

}
@media(max-width: 1400px) {

    .sliderContainer {
        margin-left: 70px;
        padding: 0;
    }

}

@media(max-width: 1290px) {

    .sliderContainer {
        margin-left: 50px;
        padding: 0;
    }

}

@media(max-width: 1024px) {

    .sliderContainer {
        margin-left: 70px;
    }
    .sliderContainer h2 {
        margin-right: 40px;
    }
}

@media(max-width: 500px) {

    .sliderContainer {
        margin-left: 40px;

    }

}

.SimpleSliderContent img {
    max-width: 282px;
    max-height: 400px;
}

.sliderContainer h2{
    font-weight: bold;
    font-size: 56px;
    line-height: 64px;
    color: #FFFFFF;

}

.SimpleSliderContent h4 {
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 32px;
    color: #FFD43C;


}

.SimpleSliderContent h3 {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 120%;
    color: #FFFFFF;


}
